<template>
  <div id="collection-create-dialog-component">
    <el-dialog
      title="Create Collection"
      v-model="visible"
      width="40%"
      top="45px"
    >
      <div class="inputs-container">
        <el-row :gutter="30">
          <el-col :span="24">
            <el-row>
              <el-col :span="11" :style="{ margin: '0' }">
                <p class="label">Privacy</p>
                <el-select v-model="form.status">
                  <el-option
                    v-for="item in statusOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-col>
              <el-col :span="2"></el-col>
              <el-col :span="11" :style="{ margin: '0' }">
                <p class="label">Show Label</p>
                <el-select v-model="form.show_label">
                  <el-option
                    v-for="item in showLabelOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-col>
            </el-row>
          </el-col>
          <el-col :span="24">
            <p class="label">Collection Name</p>
            <el-input v-model="form.name" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Owner</p>
            <el-input v-model="form.owner" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Description (optional)</p>
            <el-input v-model="form.description" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <el-row :gutter="40">
              <el-col class="nmb" :span="12">
                <p class="label">Year (optional)</p>
                <el-select v-model="form.year">
                  <el-option
                    v-for="item in yearOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-col>
              <el-col class="nmb" :span="12">
                <p class="label">Season (optional)</p>
                <el-select v-model="form.season">
                  <el-option
                    v-for="item in seasonOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-col>
            </el-row>
          </el-col>
          <!--<el-col :span="24">-->
          <!--<p class="label">Brand (optional)</p>-->
          <!--<el-input v-model="form.brand" clearable> </el-input>-->
          <!--</el-col>-->
          <!--<el-col :span="24">-->
          <!--<p class="label">Sport (optional)</p>-->
          <!--<el-input v-model="form.sport" clearable> </el-input>-->
          <!--</el-col>-->
          <el-col :span="24">
            <p class="label">Theme (optional)</p>
            <el-input v-model="form.theme" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Hashtag (optional)</p>
            <el-input v-model="form.hash_tag" clearable> </el-input>
          </el-col>
        </el-row>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="info" @click="visible = false"
            >Cancel</el-button
          >
          <el-button type="success" @click="onCreateConfirm">
            Done
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { useStore } from "vuex";

import { CollectionCreateInputs } from "@/typings/collection";
import { PostCreateCollection } from "@/typings/store";
import {
  seasonOptions,
  showLabelOptions,
  statusOptions,
  yearOptions
} from "@/views/Collection/options";

const Component = defineComponent({
  name: "CreateDialog",
  setup(_, { emit }) {
    const store = useStore();

    const visible = ref(false);
    const form = reactive<CollectionCreateInputs>({
      status: "private",
      show_label: false,
      name: "",
      owner: "",
      description: "",
      year: "",
      season: "",
      theme: "",
      hash_tag: ""
    });

    const onCreateConfirm = async () => {
      const params = {
        name: form.name,
        description: form.description,
        year: form.year,
        season: form.season,
        theme: form.theme,
        hash_tag: form.hash_tag,
        status: form.status,
        owner: form.owner,
        // TODO remove creator when api fixed.
        creator: 4,
        sport_type: []
      };
      await store.dispatch(PostCreateCollection, params);
      visible.value = false;
      emit("load");
    };

    const open = () => {
      visible.value = true;
    };

    return {
      form,
      visible,
      statusOptions,
      showLabelOptions,
      yearOptions,
      seasonOptions,

      open,
      onCreateConfirm
    };
  }
});

export default Component;
</script>

