<template>
  <div id="collection-duplicate-dialog-component">
    <el-dialog
      title="Duplicate Collection"
      v-model="visible"
      :width="step === 1 ? '80%' : '40%'"
      top="45px"
    >
      <!--Step 1-->
      <div class="step-1-container" v-if="step === 1">
        <div class="input-item">
          <p class="label">Collection Name:</p>
          <el-input v-model="form.name" clearable> </el-input>
        </div>
        <el-row :gutter="60" v-if="detail">
          <el-col
            v-for="item in detail.collection_fabrics"
            :key="item.fabric.id"
            :span="6"
          >
            <FabricCardSelectable
              :data="item.fabric"
              :selected="form.fabrics.includes(item.fabric.id)"
              @select="onCardSelect"
            ></FabricCardSelectable>
          </el-col>
        </el-row>
      </div>

      <!--Step 2-->
      <div class="step-2-container" v-if="step === 2">
        <el-row :gutter="30">
          <el-col :span="24">
            <el-row>
              <el-col :span="11" :style="{ margin: '0' }">
                <p class="label">Privacy</p>
                <el-select v-model="form.status">
                  <el-option
                    v-for="item in statusOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-col>
              <el-col :span="2"></el-col>
              <el-col :span="11" :style="{ margin: '0' }">
                <p class="label">Show Label</p>
                <el-select v-model="form.show_label">
                  <el-option
                    v-for="item in showLabelOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-col>
            </el-row>
          </el-col>
          <el-col :span="24">
            <p class="label">Collection Name</p>
            <el-input v-model="form.name" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Owner</p>
            <el-input v-model="form.owner" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Description (optional)</p>
            <el-input v-model="form.description" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <el-row :gutter="40">
              <el-col class="nmb" :span="12">
                <p class="label">Year (optional)</p>
                <el-select v-model="form.year">
                  <el-option
                    v-for="item in yearOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-col>
              <el-col class="nmb" :span="12">
                <p class="label">Season (optional)</p>
                <el-select v-model="form.season">
                  <el-option
                    v-for="item in seasonOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-col>
            </el-row>
          </el-col>
          <el-col :span="24">
            <p class="label">Theme (optional)</p>
            <el-input v-model="form.theme" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Hashtag (optional)</p>
            <el-input v-model="form.hash_tag" clearable> </el-input>
          </el-col>
        </el-row>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button v-if="step === 1" type="info" @click="visible = false">
            Cancel
          </el-button>
          <el-button v-if="step === 2" type="info" @click="step = 1">
            Back
          </el-button>
          <el-button v-if="step === 1" type="success" @click="step = 2">
            Next
          </el-button>
          <el-button v-if="step === 2" type="success" @click="onDoneClick">
            Done
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive, computed } from "vue";
import { useStore } from "vuex";
import FabricCardSelectable from "@/components/FabricCardSelectable.vue";
import { GetCollection, PostCollectionDuplicate } from "@/typings/store";
import {
  seasonOptions,
  showLabelOptions,
  statusOptions,
  yearOptions
} from "@/views/Collection/options";
import {
  CollectionDuplicateParams,
  CollectionFabrics
} from "@/typings/collection";
import { ElNotification } from "element-plus";
import { FabricCardSelectableEmit } from "@/typings/fabric";

const Component = defineComponent({
  name: "CreateDialog",
  components: {
    FabricCardSelectable
  },
  setup(_, { emit }) {
    const store = useStore();

    const visible = ref(false);
    const id = ref(-1);
    const form = reactive<CollectionDuplicateParams>({
      fabrics: [],
      name: "",
      status: "private",
      show_label: false,
      description: "",
      year: 0,
      season: "",
      theme: "",
      hash_tag: "",
      owner: 1,
      company: 1,
      joined_member: [],
      sport_type: [],
      creator: 1
    });
    const step = ref(1);

    const detail = computed(() => store.state.collection.detail);

    const init = async (id: number) => {
      await store.dispatch(GetCollection, id);

      form.fabrics = detail.value.collection_fabrics.map(
        (item: CollectionFabrics) => item.fabric.id
      );
      form.name = detail.value.name;
      form.description = detail.value.description;
      form.year = detail.value.year;
      form.season = detail.value.season;
      form.theme = detail.value.theme;
      form.hash_tag = detail.value.hash_tag;
      form.status = detail.value.status;
      form.owner = detail.value.owner;
      form.show_label = detail.value.show_label;
    };

    const open = async (_id: number) => {
      step.value = 1;
      id.value = _id;
      visible.value = true;
      await init(_id);
    };

    const onCardSelect = ({ id, selected }: FabricCardSelectableEmit) => {
      if (selected) {
        form.fabrics = form.fabrics.filter(_id => _id !== id);
      } else {
        form.fabrics = [...form.fabrics, id];
      }
    };

    const onDoneClick = async () => {
      try {
        await store.dispatch(PostCollectionDuplicate, form);
        ElNotification({
          type: "success",
          title: "Success",
          message: `Duplicate ${form.name} successful.`
        });
        emit("load");
      } catch (e) {
        ElNotification({
          type: "error",
          title: "Error",
          message: `Duplicate ${form.name} Failed.`
        });
      }
      visible.value = false;
    };

    return {
      form,
      detail,
      visible,
      step,
      seasonOptions,
      showLabelOptions,
      statusOptions,
      yearOptions,

      open,
      onCardSelect,
      onDoneClick
    };
  }
});

export default Component;
</script>

