
import { defineComponent, ref, computed, watch, onMounted, Ref } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";

import { loading } from "@/utils";
import FilterTag from "@/components/FilterTag.vue";
import CollectionCard from "@/components/CollectionCard.vue";
import CollapseItem from "@/components/CollapseItem.vue";
import DuplicateDialog from "./DuplicateDialog.vue";
import CreateDialog from "./CreateDialog.vue";

import {
  closeAllTags,
  closeTag,
  createFilterOptions,
  watchFilterOptions
} from "@/utils/asidtFilter";
import { GetCollectionFilters, GetCollections } from "@/typings/store";
import { FilterOption } from "@/typings/asideFilter";
import { CollectionsParams } from "@/typings/collection";
import { iCreateDialog } from "@/typings/component";

const createFabricsParams = (options: FilterOption[]): CollectionsParams => {
  const _options = options.reduce((target: any, option: FilterOption) => {
    target[option.title] = option.items
      .filter(item => item.checked)
      .reduce((str, item, index) => {
        if (index === 0) {
          return str + `${item.value}`;
        }
        return str + `,${item.value}`;
      }, "");
    return target;
  }, {});
  const params: CollectionsParams = {};
  if (_options["Seasons"]) params.seasons = _options["Seasons"];
  if (_options["Sports"]) params.sports = _options["Sports"];
  if (_options["Years"]) params.years = _options["Years"];
  return params;
};

const Component = defineComponent({
  name: "Collections",
  components: {
    FilterTag,
    CollectionCard,
    CollapseItem,
    DuplicateDialog,
    CreateDialog
  },
  setup() {
    const router = useRouter();
    const store = useStore();
    const createDialog: Ref<iCreateDialog | null> = ref(null);
    const duplicateDialog: Ref<iCreateDialog | null> = ref(null);

    const filterOptions = ref<FilterOption[]>([]);
    const searchContent = ref("");
    const tags = ref<string[]>([]);
    const hasAside = ref(true);
    const hasOpenButton = ref(false);
    const colorIndex = ref(-1);

    const list = computed(() => store.state.collection.list);

    watch<FilterOption[][]>(
      [filterOptions, searchContent],
      async ([options, q], [prevOption, prevQ]) => {
        if (!prevOption.length && !prevQ) return;
        tags.value = watchFilterOptions(options);
        const params = { ...createFabricsParams(options), q };
        await store.dispatch(GetCollections, params);
      },
      { deep: true }
    );

    const init = async () => {
      const loadingInstance = loading();

      try {
        await store.dispatch(GetCollectionFilters);
        await store.dispatch(GetCollections, {});
        const data = store.state.collection.filters;
        filterOptions.value = createFilterOptions(data, [
          { title: "Seasons", key: "seasons" },
          { title: "Sports", key: "sports" },
          { title: "Years", key: "years" }
        ]);
      } finally {
        loadingInstance.close();
      }
    };

    const onLoad = async () => {
      const loadingInstance = loading();
      try {
        const params = createFabricsParams(filterOptions.value);
        await store.dispatch(GetCollections, params);
      } finally {
        loadingInstance.close();
      }
    };

    onMounted(async () => init());

    const onHideButtonClick = (value: boolean) => {
      hasAside.value = value;
      if (!value) {
        setTimeout(() => {
          hasOpenButton.value = true;
        }, 300);
      } else {
        hasOpenButton.value = false;
      }
    };
    const onCreateClick = () => {
      if (!createDialog.value) return;
      createDialog.value.open();
    };
    const onAnalysisClick = async () => {
      await router.push({ name: "CollectionsAnalysis" });
    };
    const onTagCloseClick = (tag: string) => {
      filterOptions.value = closeTag(filterOptions.value, tag);
    };
    const onCloseAllTags = () => {
      filterOptions.value = closeAllTags(filterOptions.value);
    };
    const onCardClick = async (id: number) => {
      await router.push({
        name: "CollectionDetail",
        params: { id }
      });
    };
    const onCardDuplicate = async (id: number) => {
      if (!duplicateDialog.value) return;
      duplicateDialog.value.open(id);
    };

    return {
      duplicateDialog,
      createDialog,
      filterOptions,
      list,
      tags,
      hasAside,
      hasOpenButton,
      searchContent,
      colorIndex,

      onHideButtonClick,
      onCreateClick,
      onAnalysisClick,
      onTagCloseClick,
      onCloseAllTags,
      onCardClick,
      onCardDuplicate,
      onLoad
    };
  }
});

export default Component;
