
import { defineComponent, ref, reactive, computed } from "vue";
import { useStore } from "vuex";
import FabricCardSelectable from "@/components/FabricCardSelectable.vue";
import { GetCollection, PostCollectionDuplicate } from "@/typings/store";
import {
  seasonOptions,
  showLabelOptions,
  statusOptions,
  yearOptions
} from "@/views/Collection/options";
import {
  CollectionDuplicateParams,
  CollectionFabrics
} from "@/typings/collection";
import { ElNotification } from "element-plus";
import { FabricCardSelectableEmit } from "@/typings/fabric";

const Component = defineComponent({
  name: "CreateDialog",
  components: {
    FabricCardSelectable
  },
  setup(_, { emit }) {
    const store = useStore();

    const visible = ref(false);
    const id = ref(-1);
    const form = reactive<CollectionDuplicateParams>({
      fabrics: [],
      name: "",
      status: "private",
      show_label: false,
      description: "",
      year: 0,
      season: "",
      theme: "",
      hash_tag: "",
      owner: 1,
      company: 1,
      joined_member: [],
      sport_type: [],
      creator: 1
    });
    const step = ref(1);

    const detail = computed(() => store.state.collection.detail);

    const init = async (id: number) => {
      await store.dispatch(GetCollection, id);

      form.fabrics = detail.value.collection_fabrics.map(
        (item: CollectionFabrics) => item.fabric.id
      );
      form.name = detail.value.name;
      form.description = detail.value.description;
      form.year = detail.value.year;
      form.season = detail.value.season;
      form.theme = detail.value.theme;
      form.hash_tag = detail.value.hash_tag;
      form.status = detail.value.status;
      form.owner = detail.value.owner;
      form.show_label = detail.value.show_label;
    };

    const open = async (_id: number) => {
      step.value = 1;
      id.value = _id;
      visible.value = true;
      await init(_id);
    };

    const onCardSelect = ({ id, selected }: FabricCardSelectableEmit) => {
      if (selected) {
        form.fabrics = form.fabrics.filter(_id => _id !== id);
      } else {
        form.fabrics = [...form.fabrics, id];
      }
    };

    const onDoneClick = async () => {
      try {
        await store.dispatch(PostCollectionDuplicate, form);
        ElNotification({
          type: "success",
          title: "Success",
          message: `Duplicate ${form.name} successful.`
        });
        emit("load");
      } catch (e) {
        ElNotification({
          type: "error",
          title: "Error",
          message: `Duplicate ${form.name} Failed.`
        });
      }
      visible.value = false;
    };

    return {
      form,
      detail,
      visible,
      step,
      seasonOptions,
      showLabelOptions,
      statusOptions,
      yearOptions,

      open,
      onCardSelect,
      onDoneClick
    };
  }
});

export default Component;
