
import { defineComponent, ref, reactive } from "vue";
import { useStore } from "vuex";

import { CollectionCreateInputs } from "@/typings/collection";
import { PostCreateCollection } from "@/typings/store";
import {
  seasonOptions,
  showLabelOptions,
  statusOptions,
  yearOptions
} from "@/views/Collection/options";

const Component = defineComponent({
  name: "CreateDialog",
  setup(_, { emit }) {
    const store = useStore();

    const visible = ref(false);
    const form = reactive<CollectionCreateInputs>({
      status: "private",
      show_label: false,
      name: "",
      owner: "",
      description: "",
      year: "",
      season: "",
      theme: "",
      hash_tag: ""
    });

    const onCreateConfirm = async () => {
      const params = {
        name: form.name,
        description: form.description,
        year: form.year,
        season: form.season,
        theme: form.theme,
        hash_tag: form.hash_tag,
        status: form.status,
        owner: form.owner,
        // TODO remove creator when api fixed.
        creator: 4,
        sport_type: []
      };
      await store.dispatch(PostCreateCollection, params);
      visible.value = false;
      emit("load");
    };

    const open = () => {
      visible.value = true;
    };

    return {
      form,
      visible,
      statusOptions,
      showLabelOptions,
      yearOptions,
      seasonOptions,

      open,
      onCreateConfirm
    };
  }
});

export default Component;
